.main-profile-info {
    h1 {
        color: var(--Text, #211B3A);
        font-family: Urbanist;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        /* 116.667% */
    }

    .all-user-deatils-div {
        border-radius: 12px;
        background: #FFF;
            padding: 120px 30px 30px 30px;
                margin-top: 90px;

        position: relative;
    }
}

.info-div {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

}

.info-heading-div {
    h2 {
        color: var(--Text, #211B3A);
        font-family: Urbanist;
        font-size: 22px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        margin-bottom: 30px;
    }
}

.inf {
    display: flex;
    align-items: center;

    span {
        color: #000;
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 28px;
        /* 155.556% */
        width: 100px;
    }

    h5 {
        color: rgba(0, 0, 0, 0.78);
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        /* 155.556% */
        margin: 0px;
    }

    h6 {
        color: rgba(11, 56, 161, 0.78);
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        /* 155.556% */
        margin: 0px;
        cursor: pointer;
    }
}

.warning-box {
    display: flex;
    align-items: center;
    background-color: #fff4db;
    border-radius: 8px;
    padding: 12px;
    gap: 8px;
    color: #333;

    p {
        color: #A16800;
        font-family: Urbanist;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px;
        margin: 0;
    }
}

.form-label {
    color: var(--Text-Dark, #212121);
    font-family: Urbanist;
    font-size: var(--Font-Size-2, 14px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-10, 20px);
    letter-spacing: var(--Letter-Spacing-2, 0.1px);
}
.password-input {
  position: relative;

  input {
    width: 100%;
    padding-right: 40px;
  }

  .eye-button {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;

    img {
      width: 20px;
    }
  }
}
.profile-photo-div {
  border-radius: 999px;
  box-shadow: 1px -23px 29px -13px rgba(0, 0, 0, 0.216);
  width: max-content;
  position: absolute;
  top: -50%;
  left: 50%;
  transform: translate(-50%, 50%);
  img {
    border-radius: 999px;
    height: 150px;
    width: 150px;
  }
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.edit-icon-btn {
  position: absolute;
  bottom: 4px;
  right: -10px;
  background: rgba(200, 200, 200, 0.6);
  color: #fff;
  border: none;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.edit-icon-btn:hover {
  background: rgba(0, 0, 0, 0.8);
  svg path{
    fill: #fff;
  }
}
.preview-img {
  width: 60px;
  height: 60px;
  object-fit: cover;
}
::ng-deep image-cropper {
  max-width: 200px;
  height: 200px;
}
::ng-deep image-cropper .cropper-container .cropper-drag-box {
  background-color: rgb(0, 0, 0) !important;
}